  background-color: white;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 600px;
}

.input-group {
  display: flex;
  width: 100%;
  margin-bottom: 10px;
}
.input-group2 {
  display: flex;
  width: 100%;
  margin-bottom: 10px;
}

.input-group > .form-control {
  flex: 1 1 auto; 
  padding: 5px; 
