<template>
    <div class="add-movie-container">
      <h1 class="text-center">Adicionar Novo Filme</h1>
      <form @submit.prevent="adicionarFilme">
        <div class="mb-3">
          <label for="titulo" class="form-label">Título</label>
          <input
            type="text"
            id="titulo"
            v-model="filme.titulo"
            class="form-control"
            placeholder="Digite o título do filme"
            required
          />
        </div>
        <div class="mb-3">
          <label for="categoria" class="form-label">Categoria</label>
          <input
            type="text"
            id="categoria"
            v-model="filme.categoria"
            class="form-control"
            placeholder="Digite a categoria do filme"
            required
          />
        </div>
        <div class="mb-3">
          <label for="ano" class="form-label">Ano</label>
          <input
            type="number"
            id="ano"
            v-model="filme.ano"
            class="form-control"
            placeholder="Digite o ano de lançamento"
            required
          />
        </div>
        <div class="mb-3">
          <label for="imagem" class="form-label">Link para Imagem</label>
          <input
            type="url"
            id="imagem"
            v-model="filme.imagem"
            class="form-control"
            placeholder="Cole o link da imagem"
          />
        </div>
        <button type="submit" class="btn btn-success">Adicionar Filme</button>
      </form>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        filme: {
          titulo: '',
          categoria: '',
          ano: '',
          imagem: '',
        },
        erro: null,
      };
    },
    methods: {
      adicionarFilme() {
        if (this.filme.titulo.trim()) {
          this.verificarFilme(this.filme.titulo);
        } else {
          alert('O título do filme é obrigatório!');
        }
      },
  
      verificarFilme(titulo) {
        fetch(`https://api.collectapi.com/imdb/imdbSearchByName?query=${titulo}`, {
          method: 'GET',
          headers: {
            'content-type': 'application/json',
            authorization: 'apikey 4GVSSV51QMdmm7cbBSeMXi:4lccEU790kYCRqAL9rWzI2', 
          },
        })
          .then((response) => response.json())
          .then((data) => {
            if (data.success && data.result.length > 0) {
              const filmeIMDb = data.result[0];
  
              const filme = {
                imdbID: filmeIMDb.imdbID,
                Title: filmeIMDb.Title || this.filme.titulo,
                Category: this.filme.categoria || 'N/A',
                Year: this.filme.ano || filmeIMDb.Year,
                Poster: this.filme.imagem || filmeIMDb.Poster,
              };
  
              this.gravarFilme(filme);
            } else {
              this.erro = 'Filme não encontrado na IMDb';
              alert(this.erro);
            }
          })
          .catch((error) => {
            this.erro = 'Erro ao buscar dados da IMDb. Tente novamente mais tarde.';
            alert(this.erro);
            console.error('Erro ao buscar dados da IMDb:', error);
          });
      },
  
      gravarFilme(filme) {
        const filmes = JSON.parse(localStorage.getItem('filmes')) || [];
        filmes.push(filme);
        localStorage.setItem('filmes', JSON.stringify(filmes));
        alert('Filme adicionado com sucesso!');
        this.resetar();
        this.$router.push('/'); 
      },
  
      resetar() {
        this.filme.titulo = '';
        this.filme.categoria = '';
        this.filme.ano = '';
        this.filme.imagem = '';
      },
    },
  };
  </script>
  
  <style scoped>
  .add-movie-container {
    background-color: white;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    max-width: 600px;
    margin: 50px auto;
  }
  
  .text-center {
    text-align: center;
    color: #9610bb;
  }
  
  .mb-3 {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  
  .mb-3 > label {
    flex: 1 1 50%;
    padding: 5px;
    box-sizing: border-box;
  }
  
  .mb-3 > .form-control {
    flex: 1 1 50%;
    padding: 5px;
  }
  
  button {
    display: block;
    width: 100%;
  }
  </style>
  