import { createRouter, createWebHistory } from 'vue-router';
import PaginaUm from '../view/PaginaUm.vue';
import PaginaDois from '../view/PaginaDois.vue';
import AdicionarFilme from '../view/AdicionarFilme.vue'; 
import FilmesLocais from '../view/FilmesLocais.vue'; 

const routes = [
  { path: '/', name: 'Bem Vindo', component: PaginaUm },
  { path: '/pagina2', name: 'Pesquisa', component: PaginaDois },
  { path: '/adicionar-filme', name: 'AdicionarFilme', component: AdicionarFilme }, 
  { path: '/filmes-locais', name: 'FilmesLocais', component: FilmesLocais }, 
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
