<style scoped>
@import '@/PaginaDoisCss.css';
</style>

<template>
  <div class="container mt-5">
    <h1 class="text-center mb-4">
      Resultados da pesquisa para: <span>{{ pesquisa }}</span>
    </h1>
    <div id="filmeList" class="row row-cols-1 row-cols-md-3 g-4">
      <div v-for="filme in filmes" :key="filme.imdbID" class="col">
        <div class="card filme-card">
          <a
            :href="`https://www.imdb.com/title/${filme.imdbID}`"
            target="_blank"
            rel="noopener noreferrer"
          >
            <img
              :src="filme.Poster"
              class="card-img-top"
              :alt="filme.Title"
              @error="onImagemErro"
            />
          </a>
          <div class="card-body">
            <h5 class="card-title">{{ filme.Title }}</h5>
          </div>
        </div>
      </div>
    </div>
    <p v-if="erro" class="text-center text-danger mt-4">{{ erro }}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pesquisa: '',
      filmes: [],
      erro: null,
    };
  },
  mounted() {
    this.pesquisa = localStorage.getItem('pesquisa') || 'Nenhuma pesquisa realizada';

    document.title = this.pesquisa
      ? `Pesquisa IMDB - ${this.pesquisa}`
      : 'Pesquisa IMDB - Nenhum título encontrado';

    if (this.pesquisa !== 'Nenhuma pesquisa realizada') {
      this.buscarFilmes();
    }
  },
  methods: {
    buscarFilmes() {
      fetch(`https://api.collectapi.com/imdb/imdbSearchByName?query=${this.pesquisa}`, {
        method: 'GET',
        headers: {
          'content-type': 'application/json',
          authorization: 'apikey 4GVSSV51QMdmm7cbBSeMXi:4lccEU790kYCRqAL9rWzI2',
        },
      })
        .then((response) => response.json())
        .then((data) => {
          if (data.success && data.result.length > 0) {
            this.filmes = data.result;
          } else {
            throw new Error('Nenhum resultado encontrado');
          }
        })
        .catch((error) => {
          this.erro = `Erro ao carregar os filmes: ${error.message}. Por favor, tente novamente mais tarde.`;
          console.error('Erro ao buscar dados da API:', error);
        });
    },
    onImagemErro(event) {
      event.target.src = 'https://via.placeholder.com/300x450?text=Sem+Imagem';
    },
  },
};
</script>
