<template>
  <div class="container mt-5">
    <h1 class="text-center mb-4">Filmes Locais</h1>

    <div class="mb-3">
      <input
        type="text"
        v-model="pesquisa"
        class="form-control search-input"
        placeholder="Pesquise por título do filme"
        @input="filtrarFilmes"
      />
    </div>

    <div id="listaDeFilmes" class="row">
      <div v-for="filme in filmes" :key="filme.imdbID" class="col">
        <div class="card filme-card">
          <a :href="`https://www.imdb.com/title/${filme.imdbID}`" target="_blank" rel="noopener noreferrer">
            <img
              :src="filme.Poster"
              class="card-img-top"
              :alt="filme.titulo"
              @error="onImagemErro"
            />
          </a>
          <div class="card-body">
            <h5 class="card-title">{{ filme.titulo}}</h5>
          </div>
        </div>
      </div>
    </div>
    <p v-if="erro" class="text-center text-danger mt-4">{{ erro }}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pesquisa: '', 
      filmes: [], 
      filmesOriginais: [], 
      erro: null,
    };
  },
  created() {
    const filmesSalvos = JSON.parse(localStorage.getItem('filmes')) || [];
    this.filmes = filmesSalvos;
    this.filmesOriginais = filmesSalvos;
  },
  methods: {
    filtrarFilmes() {
      if (this.pesquisa.trim() === '') {
        this.filmes = this.filmesOriginais;
      } else {
        this.filmes = this.filmesOriginais.filter((filme) => 
          filme.titulo?.toLowerCase().includes(this.pesquisa.toLowerCase())
        );
      }
    },
    onImagemErro(evento) {
      evento.target.src = 'https://via.placeholder.com/300x450?text=Sem+Imagem';
    },
  },
};
</script>


<style scoped>
@import '@/FilmesLocais.css';
</style>